import { ChangeDetectorRef, Component, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BulkOperationsService } from '@c8y/ngx-components/operations/bulk-operations-service';

@Component({
  selector: 'bulk-operations-stepper-container',
  templateUrl: 'bulk-operations-stepper-container.component.html'
})
export class BulkOperationsStepperContainerComponent implements OnInit {
  @ViewChild('container', { static: false, read: ViewContainerRef }) container;
  bulkOperationType: string;

  private component: any;
  constructor(
    private activatedRoute: ActivatedRoute,
    private bulkOpsService: BulkOperationsService,
    private changeDetector: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.activatedRoute.params.subscribe(params => {
      if (params && params.type) {
        this.bulkOperationType = params.type;

        this.component = this.bulkOpsService
          .getBulkTypes()
          .find(element => element.path.includes(this.bulkOperationType)).component;

        this.changeDetector.detectChanges();
        this.createComponent(this.component);
      }
    });
  }

  private createComponent(component) {
    if (component) {
      this.container.createComponent(component);
    }
  }
}
